import axios from "axios";
import qs from "qs";

export const getRecipe = (id) => axios.get(`https://recipe-said.herokuapp.com/getrecipe/${id}`);
export const getAllRecipe = () => axios.get('https://recipe-said.herokuapp.com/getallrecipe');
export const createRecipe = (newRecipe) => axios.post('https://recipe-said.herokuapp.com/createrecipe', qs.stringify(newRecipe), { headers: { 'Content-Type': 'application/x-www-form-urlencoded' } });
export const deleteRecipe = (id) => axios.delete(`https://recipe-said.herokuapp.com/deleterecipe/${id}`);
export const favourite = (id) => axios.patch(`https://recipe-said.herokuapp.com/favourite/${id}`);
export const getFavourited = () => axios.get('https://recipe-said.herokuapp.com/getfavourited');